#blogPage {
    width: 100%;
    margin: auto;
}

#blogCard .cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 20px;
    
}

#blogCard .card {
    display: grid;
   grid-template-rows: max-content 200px 1fr;
    box-shadow: 0px 5px 5px 5px rgba(68, 68, 68, 0.2) ;
    
}

#blogCard .btn {
    text-decoration: none;
    color: #EEE;

}

#blogCard h5 {
    font-size: 32px;
    font-weight: bold;
}

@media screen and (max-width: 900px) {
    #blogCard h5 {
        font-size: 28px;
    }

    #blogCard p {
        font-size: 16px;
       
    }
}



@media screen and  (max-width: 500px) {
    #blogCard h5 {
        font-size: 24px;
    }
    #blogCard p {
        font-size: 14px;
       
    }
}
